export type StripCommentsOutput = {
  name: string;
  transform: (text: string) => { code: string; map: string | null };
};

export default function stripComments(): StripCommentsOutput {
  const all = /((?=\/\*)([\s\S]*?)\*\/)|((?=\/\/)([\s\S]*?)(?=\n))/gmi;
  return {
    name: "vite-plugin-strip-comments",
    transform(text) {
      return {
        // code: text.replace(/((?=\/\*)([\s\S]*?)\*\/)|((?=\/\/)([\s\S*?])\n)/gm, ""), // working with all
        // (^(?=\/\*)(?!(?!(legal|license))).*?\*\/$)|(^(?=\/\/)(?!(?=(legal|license))).*?\n$)
        // ^(?=\/\*|\/\/).*(?!legal|license).*(?=(\*\/|\n.+))$
        // code: text.replace(/((?=\/\*)(?!legal)([\s\S]*?)\*\/)|((?=\/\/)(?!legal)([\s\S])\n)/g, ""),
        // code: text.replace(/^(?=\/\*|\/\/).*(?!(legal|license)).*(?=(\*\/|\n.+))$/g, ""),
        code: text.replace(all, ""),
        map: null,
      };
    },
  };
}
